export interface IProduct {
    id: number;
    name: string;
    type: string;
    image: string;
    longDescription: string;
    specs: ISpecs;
    amount?: number;
}
export interface ISpecs {
    actuationForce: string;
    actuationPoint: string;
    bottomOut: string;
    bottomOutTravel: string;
    price: string;
}

export interface ICartItemPostData {
    productId: number;
    amount: number;
}

export class CartCollection {
    items: IProduct[] = [];
    constructor() {
        // console.log(`VUE: CartCollection constructor`);
        // this.items = [
        //     new CartItem(
        //         1,
        //         "Holy Panda",
        //         "70",
        //         1.60,
        //         "tactile",
        //         "holy_panda.png"
        //     ),
        //     new CartItem(3, "Kiwis", "70", 0.95, "tactile", "kiwis.png"),
        //     new CartItem(7, "Zilent 67g", "110", 1.80, "tactile", "zilent_67.png")
        // ];
    }
    getCartItemsPostData() {
        let returnData = [];
        for (let item of this.items) {
            returnData.push({ productId: item.id, amount: item.amount });
        }
        return { items: returnData };
    }
}